.case-studies {

	.case-study-preview {

		.case-study-preview-title {

			&-label {
				color: transparentize($white, 0.35);
			}

		}

		&:nth-child(4n-3) .case-study-preview-title {

			background: $green;

			// &-label {
			// 	color: $green-highlight;
			// }

		}

		&:nth-child(4n-2) .case-study-preview-title {

			background: $blue;

			// &-label {
			// 	color: $blue-highlight;
			// }

		}

		&:nth-child(4n-1) .case-study-preview-title {

			background: $red;

			// &-label {
			// 	color: $red-highlight;
			// }

		}

		&:nth-child(4n) .case-study-preview-title {
			background: $grey-light;
		}

		&-title {

			padding: 1.5rem;
			color: $white;
			font-weight: 500;
			font-size: 1.25rem;

			&-label {
				margin-bottom: 0.5rem;
				font-size: 0.625em;
				@include caps();
			}

		}

		&-description {
			padding: 1.5rem;
		}

		&-images-wrap {

			margin: 0px 1.5rem;

			figure {
				margin: 0px;
			}

			img {
				display: block;
				margin: 0px auto;
				width: 100%;
				max-width: 30rem;
				// border: 1px solid $grey-lighter;
				box-shadow: 0px 0.125rem 0.5rem -0.25rem transparentize($black, 0.6);
			}

		}

		+ .case-study-preview {
			margin-top: 4rem;
		}

		&:last-child {
			margin-bottom: 4rem;
		}

	}

	@media screen and (min-width: $mobile-breakpoint) {

		$marginUnit: 4rem;

		.case-study-preview {

			position: relative;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			padding: $marginUnit 0px;

			&:nth-child(odd):before {
				content: ' ';
				z-index: -1;
				position: absolute;
				left: 50%;
				top: 0px;
				width: 100vw;
				height: 100%;
				transform: translate(-50%, 0px);
				background: $grey-lightest;
			}

			&-content {
				flex-basis: 55%;
			}

			&-title {
				padding: 2rem $marginUnit;
				font-size: 2rem;
			}

			&-description {

				padding: 2rem $marginUnit;
				font-size: 1.75rem;

				.btn {
					font-size: 1.25rem;
				}

			}

			&-images-wrap {

				margin: 0px $marginUnit 0px 0px;
				flex-basis: calc(45% - #{$marginUnit * 2});

				img {
					max-width: 100%;
				}

			}

			&:nth-child(even) {

				.case-study-preview-images-wrap {
					order: 1;
					margin: 0px 0px 0px $marginUnit;
				}

				.case-study-preview-content {
					order: 2;
				}

			}

			+ .case-study-preview {
				margin-top: 3rem;
			}

			&:last-child {
				margin-bottom: 3rem;
			}

		}

	}

	@media screen and (min-width: $screen-lg) {

		.case-study-preview {

			&-title {
				padding-top: 3rem;
				padding-bottom: 3rem;
			}

		}

	}

	@media screen and (min-width: $screen-xl) {

		.case-study-preview {

			&-title {
				padding-top: 4rem;
				padding-bottom: 4rem;
			}

		}

	}

}

.page-case-study {

	position: relative;

	&:before {
		content: ' ';
		z-index: 999;
		position: fixed;
		top: 0px;
		left: 0px;
		right: 100%;
		height: 0.25rem;
		animation: {
			name: topBorderLeftToRight;
			duration: $animation-speed * 1.5;
			timing-function: linear;
			fill-mode: forwards;
		}
		will-change: right;
	}

	&.case-study-meal-plans:before {
		background: $green;
	}

	&.case-study-sdc:before {
		background: $blue;
	}

	&.case-study-kellyinthecity:before {
		background: $red;
	}

	&.case-study-civilian:before {
		background: $grey-light;
	}

}
